.input-group{
  margin-bottom: 15px;
  .col-6{
    width: 50%;
    padding-left: 15px;
    padding-right: 15px;
    float: left;
  }
  input, textarea{
    background: #fff;
    border: 1px solid #3a54b4;
    border-radius: 6px;
    padding: 7px;
    padding-left: 12px;
    font-size: 15px;
    width: 100%;
    &::-webkit-input-placeholder { /* Edge */
      color: #222;
    }

    &:-ms-input-placeholder { /* Internet Explorer 10-11 */
      color: #222;
    }

    &::placeholder {
      color: #222;
    }
  }
  /* The container */
  .wpcf7-list-item-label {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 0;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    line-height: 36px;
  }

  /* Hide the browser's default checkbox */
  .checkbox__group input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  /* Create a custom checkbox */
  .wpcf7-list-item-label:before {
    position: absolute;
    top: 8px;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: #ccc;
  }

  /* When the checkbox is checked, add a blue background */
  .checkbox__group input:checked ~ .wpcf7-list-item-label:before {
    background-color: #2196F3;
  }

  /* Create the checkmark/indicator (hidden when not checked) */
  .checkbox__group .wpcf7-list-item-label:after {
    content: "";
    position: absolute;
    display: none;
  }

  /* Show the checkmark when checked */
  .checkbox__group input:checked ~ .wpcf7-list-item-label:after{
    display: block;
  }

  /* Style the checkmark/indicator */
  .checkbox__group .wpcf7-list-item-label:after {
    left: 8px;
    top: 11px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }

  /* The container */
  .radio__group {
    display: block;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    line-height: 36px;
    float: left;
    &:last-child{
      margin-right: 0;
    }
  }

  .wpcf7-list-item{
    margin:0 20px 0 0;
  }

  .radio__group .wpcf7-list-item-label{
    padding-left: 25px;
  }
  /* Hide the browser's default radio button */
  .radio__group input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
  }

  /* Create a custom radio button */
  .radio__group .wpcf7-list-item-label:before {
    position: absolute;
    top: 9px;
    left: 0;
    height: 16px;
    width: 16px;
    background-color: #eee;
    border-radius: 50%;
  }

  /* On mouse-over, add a grey background color */
  .radio__group:hover input ~ .wpcf7-list-item-label:before {
    background-color: #ccc;
  }

  /* When the radio button is checked, add a blue background */
  .radio__group input:checked ~ .wpcf7-list-item-label:before {
    background-color: #2196F3;
  }

  /* Create the indicator (the dot/circle - hidden when not checked) */
  .radio__group .wpcf7-list-item-label:after {
    content: "";
    position: absolute;
    display: none;
  }

  /* Show the indicator (dot/circle) when checked */
  .radio__group input:checked ~ .wpcf7-list-item-label:after {
    display: block;
  }

  /* Style the indicator (dot/circle) */
  .radio__group .wpcf7-list-item-label:after {
    top: 13px;
    left: 4px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: white;
  }

  p{
    margin-bottom: 10px;
  }

  .SumoSelect>.CaptionCont{
    background: #fff;
    border: 1px solid #3a54b4;
    border-radius: 6px;
    padding: 7px;
    padding-left: 12px;
    font-size: 15px;
    width: 100%;
  }

  .submit{
    width: 100%;
    text-align: center;
    padding: 10px;
    background: #3a54b4;
    color: #fff;
    border-radius: 3px;
  }
}
.form-group{
  .row{
    .col-6{
      padding-left: 15px;
      padding-right: 15px;
      clear: both;
      @media screen and (min-width: $tablet-medium){
        width: 50%;
        float: left;
        clear: unset;
      }
    }
  }
}
.single-tuyen_sinh .tuyensinh__form{
  display: none;
}
.single-tuyen_sinh .fancybox-container .tuyensinh__form{
  display: block;
}