.banner{
  margin-top: 80px;
  .flexslider{
    border:none;
    margin-bottom: 0;
    .slides{
      background: #FEF7ED;
      li{
        position: relative;
        min-height: 600px; 
        background-size: cover !important;
        background-position: center center;
        @media screen and (min-width: $desktop-small){
          height: auto;
        }
      }
      &__item{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column-reverse;
        width: 100%;
        top: 50%;
        left: 50%;
        position: absolute;
        transform: translate(-150%, -50%);
        transition: all 1s ease .5s;
        @media screen and (min-width: $tablet-medium){
          flex-direction: row;
        }
        &--content{
          max-width: 650px;
          padding: 30px;
          background: rgba(#fff, .8);
          h2{
            font-size: 30px;
            color: #3A54B4;
            font-weight: bold;
            text-transform: uppercase;
            margin-bottom: 15px;
            line-height: 50px;
            @media screen and (min-width: $tablet-small){
              font-size: 40px;
              line-height: 60px;
            }
            @media screen and (min-width: $desktop-medium){
              font-size: 57px;
            }
          }
          .sub{
            font-size: 16px;
            text-transform: uppercase;
            margin-bottom: 15px;
          }
          a{
            padding: 10px 15px;
            color: #fff;
            background: #3A54B4;
            text-transform: uppercase;
            font-size: 16px;
            display: inline-block;
          }
        }
        .image{
          max-width: 350px;
          border-radius: 50%;
          overflow: hidden;
          margin-bottom: 20px;
          @media screen and (min-width: $tablet-medium){
            max-width: 550px;
            margin-bottom: 0;
          }
        }
      }
      .flex-active-slide{
        .slides__item{
          transform: translate(-50%, -50%);
        }
      }
    }
    .flex-control-nav{
      bottom: 20px;
    }
    .flex-direction-nav{
      display: none;
      @media screen and (min-width: $tablet-medium2){
        display: block;
      }
    }
  }
}
.introduce{
  padding: 80px 0;
  background: #FFE4E0;
  &__main{
    @media screen and (min-width: $tablet-medium){
      display: flex;
      align-items: center;
    }
    .image{
      text-align: center;
      @media screen and (min-width: $tablet-medium) {
        width: 50%;
      }
      @media screen and (min-width: $desktop-medium) {
        width: 40%;
      }
      img{
        display: inline-block;
        max-width: 100%;
        @media screen and (min-width: $tablet-small) {
          max-width: 350px;
        }
        @media screen and (min-width: $desktop-small) {
          max-width: 400px;
        }
      }
    }
    &--content{
      @media screen and (min-width: $tablet-medium) {
        width: 50%;
      }
      @media screen and (min-width: $desktop-medium) {
        width: 60%;
      }
      .main{
        max-width: 450px;
        margin: 0 auto;
        h3{
          font-size: 25px;
          font-weight: bold;
          margin-bottom: 20px;
        }
        .description{
          margin-bottom: 20px;
          line-height: 25px;
        }
        ul{
          li{
            display: flex;
            align-items: center;
            margin-bottom: 30px;
            text-align: justify;
            span{
              display: inline-block;
              margin-right: 10px;
              &:last-child{
                width: calc(100% - 50px);
              }
            }
          }
        }
      }
    }
  }
}
.programe{
  padding: 80px 0;
  position: relative;
  h3{
    font-size: 25px;
    text-align: center;
    font-weight: bold;
  }
  &__main{
    @media screen and (min-width: $tablet-medium){
      display: flex;
    }
  }
  &__item{
    text-align: center;
    padding: 0 15px;
    margin-bottom: 20px;
    @media screen and (min-width: $tablet-medium){
      width: calc(100%/3);
      margin-bottom: 0;
    }
    .image{
      border-radius: 50%;
      margin: 0 auto;
      overflow: hidden;
      margin-bottom: 20px;
      border: 10px solid #ff9000;
      width: 245px;
      height: 245px;
      @media screen and (min-width: $tablet-medium) and (max-width: $tablet-medium2){
        width: auto;
        height: auto;
      }
    }
    &:first-child{
      .image{
        border: 10px solid #038729;
      }
    }
    &:last-child{
      .image{
        border: 10px solid #7d1f96;
      }
    }
    h4{
      font-size: 15px;
      font-weight: 600;
      color: #FF9000;
      margin-bottom: 20px;
    }
  }
  .icon_bg{
    position: absolute;
    top: 0;
    right: 0;
    max-width: 140px;
  }
}
.learning{
  background: #96FAAC;
  position: relative;
  @media screen and (min-width: 768px){
    display: flex;
    align-items: center;
    overflow: hidden;
  }
  .image{
    text-align: right;
    img{
      padding-top: 50px;
      display: inline-block;
      padding-left: 50px;
      @media screen and (min-width: 768px) {
        padding-top: 0;
        top: 60px;
        left: 10%;
        position: relative;
        max-width: 100%;
        display: block;
        padding-left: 0;
      }
      @media screen and (min-width: $desktop-medium){
        left: 30%;
      }
    }
    @media screen and (min-width: 768px){
      width: 50%;
    }
  }
  &__content{
    padding: 50px 15px;
    @media screen and (min-width: $tablet-small){
      padding: 50px;
    }
    @media screen and (min-width: 768px){
      width: 50%;
    }
    @media screen and (min-width: 1400px){
      padding: 0 200px 0 120px;
    }
    h3{
      font-size: 25px;
      font-weight: bold;
    }
    ul{
      li{
        display: flex;
        align-items: center;
        margin-bottom: 30px;
        span{
          display: inline-block;
          margin-right: 10px;
          &:last-child{
            width: calc(100% - 50px);
          }
        }
      }
    }
  }
  .icon_bg{
    position: absolute;
    left: 15%;
    top: -84px;
    max-width: 140px;
    transform: rotate(27deg);
  }
}
.development{
  background: #FAFAFA;
  padding: 80px 0;
  &__main{
    max-width: 800px;
    margin: 0 auto;
    h3{
      font-size: 25px;
      font-weight: bold;
      text-align: center;
    }
  }
  &__item{
    min-height: 90px;
    font-weight: 600;
    display: flex;
    align-items: center;
    margin: 0 auto 40px;
    max-width: 300px;
    @media screen and (min-width: $tablet-medium){
      width: 50%;
      float:left;
      margin-bottom: 40px;
      max-width: unset;
    }
    .image{
      max-width: 80px;
      img{
        transform: rotate(-45deg);
        transition: all 1s ease;
      }
    }
    .description{
      margin-left: -20px;
      margin-top: 15px;
      position: relative;
      z-index: 1;
    }
    &:hover{
      .image{
        img{
          transform: rotate(45deg);
        }
      }
    }
  }
}
.mission{
  min-height: 500px;
  background-size: cover;
  background-position: bottom center;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  .container{
    z-index: 2;
  }
  &__main{
    max-width: 550px;
    color: #fff;
    text-align: center;
    position: relative;
    z-index: 1;
    padding: 50px 0;
    h3{
      font-size: 25px;
      font-weight: bold;
      text-align: center;
    }
  }
  &:after{
    content :"";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background: rgba(#000, .3);
  }
  .icon_bg{
    position: absolute;
    z-index: 1;
    top: 10%;
    right: 10%;
    transform: rotate(80deg);
    max-width: 190px;
  }
}
.gallery{
  padding: 50px 0;
  @media screen and (min-width: $tablet-medium){
    padding: 100px 0;
  }
  h3{
    font-size: 25px;
    font-weight: bold;
    text-align: center;
  }
  &__wrapper{
    margin-left: -15px;
    margin-right: -15px;
  }
  &__item{
    padding: 15px;
    width: 100%;
    text-align: center;
    @media screen and (min-width: $tablet-small){
      width: 50%;
      float: left;
    }
    @media screen and (min-width: $tablet-medium){
      width: 25%;
    }
    img{
      display: inline-block;
    }
  }
}
