/* Menu || Burger
----------------------------------------------------------------------------------------------------*/
.button__menu {

  /* Menu || Burger || Container
  ----------------------------------------------------------------------------------------------------*/
  &-container {
    position: absolute;
    top: 24px;
    right: 15px;
    padding: 5px;
    color: $white;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 400;
    @include clearfix;
    z-index: 11;
    @media screen and (min-width: $tablet-medium) {
      display: none;
    }

    button {
      cursor: pointer;
    }
    &__open {
      width: 24px;
      @include clearfix;
    }
  }

  /* Menu || Burger || Open
  ----------------------------------------------------------------------------------------------------*/
  &-open {
    display: block;
    float: left;
    position: relative;
    @include rotate(0);
    @include size(26px, 22px);
    //@include transition (all .5s ease-in-out);

    span {
      display: block;
      position: absolute;
      background: #3a54b4;
      @include size(50%, 3px);
      @include rotate(0);
      @include transition (all .25s ease-in-out);
      &:first-child, &:nth-child(2){
        top: 0;
      }
      &:nth-child(3), &:nth-child(4){
        top: 10px;
      }
      &:nth-child(5), &:nth-child(6){
        top: 20px;
      }
      &:nth-child(2n) {
        left: 50%;
        border-radius: 0 9px 9px 0;
      }
      &:nth-child(odd) {
        left: 0;
        border-radius: 9px 0 0 9px;
      }
    }
    &.open {
      span {
        &:first-child{
          left: 2px;
          top: 4px;
        }
        &:nth-child(2){
          left: calc(50% - 2px);
          top: 4px;
        }
        &:nth-child(3){
          left: -50%;
        }
        &:nth-child(4){
          left: 100%;
        }
        &:nth-child(5){
          left: 2px;
          top: 13px;
        }
        &:nth-child(6){
          left: calc(50% - 2px);
          top: 13px;
        }
        &:first-child, &:nth-child(6){
          transform: rotate(45deg);
        }
        &:nth-child(3), &:nth-child(4){
          opacity: 0;
        }
        &:nth-child(2), &:nth-child(5){
          transform: rotate(-45deg);
        }
      }
    }
  }

  /* Menu || Burger || Close
  ----------------------------------------------------------------------------------------------------*/
  &-close {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0);
    @include size(70px, 32px);

    .menu-mobile--active & {
      display: block;
    }
  }
}

header {
  .menu{
    width: 100%;
    max-width:900px;
    background-color: #fff;
    position: absolute;
    left: 0;
    top: 81px;
    z-index: 9;
    transform: translateX(-100%);
    transition: all .5s ease-out;
    border-top: 1px solid #f2dede;
    @media screen and (min-width: $tablet-medium){
      background-color: unset;
      position: unset;
      transform: unset;
      border-top: none;
    }
    &>ul{
      @media screen and (min-width: $tablet-medium){
        display: flex;
      }
      >li{
        display: block;
        position: relative;
        a{
          transition: all .5s ease;
          font-size: 15px;
          position: relative;
          font-weight: 800;
          text-transform: uppercase;
          @media screen and (min-width: $tablet-medium) and (max-width: $tablet-medium2){
            font-size: 12px;
          }
          i{
            margin-left: 5px;
            font-size: 20px;
            position: absolute;
            top: -2px;
          }
          &:hover{
            color: #FF8B7B;
          }
        }
        .sub-menu{
          display: none;
          @media screen and (min-width: $tablet-medium){
            position: absolute;
            left: 0;
            top: 100%;
            background: #f4f4f4;
            width: 300px;
            box-shadow: 1px 1px 3px 0px #ccc;
            visibility: hidden;
            opacity: 0;
            border-radius: 10px;
            transition: margin-top .3s ease-in-out .1s;
            margin-top: 20px;
            overflow: hidden;
            display: block;
          }
          li{
            a{
              display: block;
              padding: 10px 15px;
              text-transform: unset;
              font-weight: 400;
              color: #222;
              font-size: 14px;
              &:hover{
                background: #3a54b4;
                color: #fff;
              }
            }
          }
          &.active{
            display: block;
          }
        }
        &:hover{
          .sub-menu{
            @media screen and (min-width: $tablet-medium) {
              margin-top: 0px;
              visibility: visible;
              opacity: 1;
            }
          }
        }
      }
      &>li{
        padding: 10px 15px;
        @media screen and (min-width: $tablet-medium) and (max-width: $tablet-medium2){
          padding: 10px;
        }
        @media screen and (min-width: $desktop-small){
          padding: 10px 20px;
        }
        @media screen and (min-width: $desktop-medium){
          padding: 10px;
          margin-right: 20px;
        }
        &:last-child{
          @media screen and (min-width: $tablet-medium){
            display: none;
          }
          @media screen and (min-width: $tablet-medium2){
            display: block;
          }
        }
        &:first-child{
          a{
            color: #fb6b47;
          }
          ul{
            li{
              &:hover{
                a{
                  color: #fb6b47;
                  background: #fff;
                }
              }
            }
          }
        }
        &:nth-child(2){
          a{
            color: #ff911b;
          }
          ul{
            li{
              &:hover{
                a{
                  color: #ff911b;
                  background: #fff;
                }
              }
            }
          }
        }
        &:nth-child(3){
          a{
            color: #ffc000;
          }
          ul{
            li{
              &:hover{
                a{
                  background: #fff;
                  color: #ffc000;
                }
              }
            }
          }
        }
        &:nth-child(4){
          a{
            color: #a7c850;
          }
          ul{
            li{
              &:hover{
                a{
                  background: #fff;
                  color: #a7c850;
                }
              }
            }
          }
        }
        &:nth-child(5){
          a{
            color: #5bc4db;
          }
          ul{
            li{
              &:hover{
                a{
                  background: #fff;
                  color: #5bc4db;
                }
              }
            }
          }
        }
        &:nth-child(6){
          a{
            color: #7a8ed9;
          }
          ul{
            li{
              &:hover{
                a{
                  background: #fff;
                  color: #7a8ed9;
                }
              }
            }
          }
        }
      }
    }
    &.active{
      transform: translateX(0);
    }
  }
}
