body {
  font-family: $font-1; //Default font family
  color: #3A54B4; //Default text color
  letter-spacing: normal; //Default letter spacing
  font-size: 15px; //Default font size
  line-height: 22px; //Default line height
  font-weight: 400;
  overflow: hidden;
  min-height: 100%;
}
h2{
  line-height: 40px;
  &.title{
    text-transform: uppercase;
    font-family: $font-1;
    font-weight: 700;
    font-size: 25px;
    margin-bottom: 20px;
  }
}
#main__content{
  margin-top: 40px;
}
.btn {
  @include transition (all .3s ease);
  //@include hover{
  //   @include opacity(0.8);
  //}
}
.flex-direction-nav a{
  overflow: unset;
}
.alignleft{
  float: left;
  margin-right: 15px;
}
.alignright{
  float: right;
  margin-left: 15px;
}