.gallery__wrapper{
  display: flex;
  flex-wrap: wrap;
  .item{
    width: 100%;
    flex-wrap: wrap;
    padding: 15px;
    text-align: center;
    img{
    	display: inline-block;
    }
    @media screen and (min-width: 480px){
    	width: 50%;
    }
    @media screen and (min-width: 991px){
    	width: calc(100%/3);
    }
  }
}