aside{
  @media screen and (min-width: $tablet-medium){
    width: 30%;
    float: right;
  }
  ul{
    border: 1px solid #3a54b4;
    border-radius: 3px;
    li{
      width: 100%;
      padding: 0 10px;
      transition: all .5s ease;
      a{
        display: block;
        height: 50px;
        line-height: 50px;
        border-bottom: 1px dashed #3a54b4;
        position: relative;
        padding-left: 20px;
        font-size: 14px;
        color: #3a54b4;
        font-weight: 600;
        &:before{
          content: "";
          display: block;
          width: 5px;
          height: 5px;
          background: #3a54b4;
          float: left;
          position: absolute;
          top: 22px;
          left: 5px;
        }
        &:hover{
          color: #ffe4e0;
        }
      }
      &:last-child{
        a{
          border-bottom: none;
        }
      }
      &:hover{
        background: #3a54b4;
        a{
          color: #fff;
          &:before{
            background: #fff;
          }
        }
      }
    }
  }
  .image{
    margin-top: 30px;
  }
}