.main__wrapper{
  margin-top: 80px;
  .entry-header{
    background: url(../images/bg-title.jpg);
    padding: 25px 0;
    background-size: cover;
    h1{
      font-size: 25px;
      font-weight: 600;
      text-align: center;
      text-transform: uppercase;
      line-height: 38px;
    }
  }
  .main__content{
    padding: 40px 0;
    color: #222;
    &--wrapper{
      padding: 0;
      margin-bottom: 50px;
      @media screen and (min-width: $tablet-medium){
        width: 70%;
        float: left;
        padding: 0 30px 0 0;
      }
      h3{
        font-size: 18px;
        line-height: 30px;
      }
      h2, h3{
        color: #3a54b4;
        margin-bottom: 10px;
        font-weight: 600;
      }
      strong, b{
        color: #3a54b4;
      }
      a{
        color: #3a54b4;
        text-decoration: underline;
      }
      table{
        margin-bottom: 15px;
        max-width: 100%;
        tr{
          td{
            border: 1px solid #ccc;
            padding: 10px;
          }
        }
        &.guide-form{
          tr{
            td{
              &:first-child{
                text-align: center;
              }
            }
          }
        }
      }
      ol{
        list-style: decimal;
        margin-left: 15px;
        li{
          margin-bottom: 15px;
        }
      }
    }
  }
}
.page-default{
  margin: 0 auto;
  header{
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 20px;
  }
}
.entry-content, .mission__container{
  p{
    margin-bottom: 15px;
  }
  a{
    color: #3a54b4;
    text-decoration: underline;
  }
  h2{
    font-size: 22px;
    line-height: 35px;
  }
  h3{
    font-size: 18px;
    line-height: 30px;
  }
  h2, h3{
    color: #3a54b4;
    margin-bottom: 10px;
    font-weight: 600;
  }
  strong, b{
    color: #3a54b4;
  }
  ul{
    margin-left: 15px;
    li{
      margin-bottom: 15px;
      list-style: disc;
    }
  }
}
.post__item{
  text-align: center;
  @media screen and (min-width: $tablet-small){
    display: flex;
    position: relative;
    margin-bottom: 40px;
    text-align: left;
  }
  &:last-child{
    margin-bottom: 0;
  }
  .image{
    margin-bottom: 15px;
    text-align: center;
    @media screen and (min-width: $tablet-small){
      margin-right: 15px;
      margin-bottom: 0;
      width: 215px;
    }
    img{
      display: inline-block;
    }
  }
  &--content{
    @media screen and (min-width: $tablet-small){
      max-width: calc(100% - 230px);
    }
    h2{
      text-transform: uppercase;
      font-weight: 600;
      font-size: 20px;
      line-height: 28px;
    }
  }
  a{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 0;
  }
}