.clearfix {
  @include clearfix;
}

.main {
  height: 100%;
  //min-height:100%;
  //position: relative;
}

.container {
  margin: 0 auto;
  position: relative;
  padding: 0 15px;
  max-width: 1100px;
  @include clearfix;
  //@media screen and (min-width: $desktop-medium + 1) {
  //  max-width: $desktop-medium - 60;
  //}
  @media screen and (min-width: $tablet-medium) {
    padding: 0 30px;
  }
  @media screen and (min-width: $desktop-medium) {
    padding: 0;
  }

}
.row {
  position: relative;
  @include clearfix;
  margin-right: -15px;
  margin-left: -15px;
}

.col-4{
  padding-left:15px;
  padding-right: 15px;
  float:left;
  width: calc(100% / 3);
}

.form-group {
  margin-bottom: 15px;
  label {
    margin-bottom: 5px;
    display: block;
  }
  .form-control {
    border: 1px solid $color1;
    padding: 10px 20px;
    border-radius: 4px;
    background: $white;
    font-weight: 500;
    font-size: 14px;
    width: 100%;
    resize: none;
    @include placeholder {
      font-style: italic;
      color: #909195;
    }
  }
}

.animated2 {
  -webkit-animation-duration: 0.3s;
  animation-duration: 0.3s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@-webkit-keyframes fadeInDown2 {
  0% {
    opacity: 1;
    -webkit-transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

@-moz-keyframes fadeInDown2 {
  0% {
    opacity: 1;
    -moz-transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    -moz-transform: translateY(0);
  }
}

@-o-keyframes fadeInDown2 {
  0% {
    opacity: 1;
    -o-transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    -o-transform: translateY(0);
  }
}

@keyframes fadeInDown2 {
  0% {
    opacity: 1;
    transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.fadeInDown2 {
  animation-name: fadeInDown2;
}

.progress {
  height: 4px;
  overflow: hidden; // force rounded corners by cropping it
  font-size: 14px;
  background-color: $color1;
  @include flex;
}

.progress-bar {
  flex-direction: column;
  justify-content: center;
  color: $white;
  text-align: center;
  background-color: $color4;
  @include transition(width .6s ease);
  @include flex;
}

.label-radio {
  margin-top: 15px;
}

.group-radio {
  margin-bottom: 15px;
  margin-top: 10px;
}

.checkbox{
  input[type='checkbox'],
  input[type='radio']{
    @include hover{
      outline: none;
      & + label {
        &:before {
          outline: none;
          outline-offset: 0;
        }
      }
    }
  }
}
.abc-radio {
  &.form-check-inline {
    display: inline-block;
    margin-right: 20px;
    margin-bottom: 15px;
  }
  input[type=radio] {
    @include hover {
      outline: none;
      & + label {
        &:before {
          outline: none;
          outline-offset: 0;
        }
      }
    }
  }
  label {
    font-size: 15px;
    font-weight: 600;
    &:after {
      background: $color2;
    }
  }
}

.show-on-mobile {
  @media screen and (min-width: $tablet-medium) {
    display: none;
  }
}

.show-on-tablet {
  @media screen and (min-width: $desktop-small) {
    display: none;
  }
}

//backend login
body {
  &.toolbar-fixed {
    overflow: hidden;
  }

  &.page-home {
    overflow: hidden;
  }
}

.toolbar-fixed {
  .header {
    top: 79px;
    &.fixed {
      top: 79px;
    }
  }

  .content-home__left {
    .inner {
      height: calc(100% - 129px);
    }
  }
}



//popup
#popup_box{
  display: block;
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background:rgba(0,0,0,0.5);
  #popupBoxClose{
    background: url('../images/i-close.png') no-repeat;
    width: 36px;
    height: 37px;
    position: absolute;
    top: 0;
    right: 0;
    text-indent: -999px;
    padding: 10px;
    background-position: center center;
    cursor: pointer;
  }
  .popup_box_main{
    margin: 0 auto;
    width: 100%;
    height: 200px;
    display: block;
    position: absolute;
    left: 0;
    top: 50%;
    right: 0;
    bottom: 0;
    @include transform(translateY(-50%));
    padding: 30px;
    background: $color3;
    border-radius: 4px;
    overflow-y: auto;
    @media screen and (min-width: $tablet-medium){
      max-width: 500px;
      height: 200px;
    }
    #block-sharethiswidget-2{
      margin: 0 auto;
      text-align: center;
      position: absolute;
      left: 0;
      top: 50%;
      right: 0;
      @include transform(translateY(-50%));
    }
    .sharethis-wrapper{
      text-align: center;
      position: absolute;
      left: 0;
      top: 50%;
      right: 0;
      @include transform(translateY(-50%));
    }
  }
}