.product{
  &__item{
    padding: 0 15px;
    margin-bottom: 30px;
    width: 50%;
    @media screen and (min-width: $tablet-medium){
      width: calc(100% /3);
    }
    @media screen and (min-width: $desktop-medium){
      width: 25%;
    }

    &--main{
      background: #fff;
      position: relative;
      transition: all .3s ease-in;
      a{
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
      }
      .info{
        padding: 15px;
        min-height: 125px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        h3{
          font-weight: 700;
          margin-bottom: 5px;
        }
        p{
          span{
            margin-right: 10px;
            display: inline-block;
          }
        }
      }

      &:hover{
        box-shadow: 3px 3px 10px #ccc;
      }
    }
  }
}