.footer{
  padding: 50px 0;
  background: #ffc000;
  @media screen and (min-width: $tablet-medium2){
    padding: 100px 0;
  }
  .container{
    z-index: 1;
  }
  &__main{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-direction: column;
    @media screen and (min-width: $tablet-medium2){
      flex-direction: row;
    }
  }
  .logo{
    width: 145px;
    margin-bottom: 20px;
    @media screen and (min-width: $tablet-medium2){
      margin-bottom: 0;
    }
  }
  &__content{
    max-width: 260px;
    @media screen and (min-width: $tablet-medium2){
      margin-left: 100px;
    }
    h4{
      font-size: 20px;
      font-weight: 600;
      margin-bottom: 30px;
    }
    ul{
      li{
        margin-bottom: 15px;
        span{
          display: inline-block;
          font-weight: 600;
        }
      }
    }
  }
  &__form{
    .description{
      margin-bottom: 15px;
    }
  }
}
.footer__event{
  position: relative;
  &:after{
    content: "";
    top: 0;
    left:0;
    bottom:0;
    right: 0;
    background: rgba(0,0,0,.3);
    position: absolute;
  }
  &--main{
    min-height: 150px;
    padding: 50px 0;
    position: relative;
    z-index: 1;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    @media screen and (min-width: $tablet-medium){
      flex-direction: row;
      justify-content: space-between;
      text-align: left;
    }
    .content{
      margin-bottom: 20px;
      @media screen and (min-width: $tablet-medium){
        margin-bottom: 0;
      }
      .description{
        font-size: 17px;
      }
    }
    h3{
      margin-bottom: 10px;
    }
    a{
      background: #ffc000;
      padding: 15px 20px;
      border: 1px solid #ffc000;
      text-transform: uppercase;
      font-weight: 600;
      font-size: 16px;
      &:hover{
        color: #ffc000;
        background: #fff;
      }
    }
  }
}