#header{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
  background: #fff;
  box-shadow: 1px 1px 5px #ccc;
  &.scroll-down{
    box-shadow: 1px 1px 5px #ccc;
  }
}
.header{
  display: flex;
  align-items: center;
  padding: 15px 0;
  .logo{
    display: block;
    max-width: 145px;
    margin-right: 50px;
  }
  .youtube__social{
    color: #FF0000;
  }
  &__social{
    display: flex;
    position: absolute;
    top: 26px;
    right: 60px;
    @media screen and (min-width: $tablet-medium){
      position: unset;
    }
    li{
      margin-right: 10px;
      &:last-child{
        margin-right: 0;
      }
      a{
        font-size: 30px;
      }
    }
  }
}